html {
  --main-color: #00acdc;
  --header-height: 60px;
  font-family: Roboto, Raleway, sans-serif;
  font-size: 12px;
  line-height: 15px;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo_image {
  width: 40px;
  height: 40px;
  display: flex;
  position: relative;
  left: 50px;
}

.nav {
  position: relative;
  right: 50px;
}

.nav__list {
  justify-content: space-between;
  width: 639px;
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  position: relative;
}

li > a {
  color: #000;
  text-transform: uppercase;
  height: var(--header-height);
  align-items: center;
  display: flex;
}

a.is-active {
  color: var(--main-color);
  display: flex;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

a:hover {
  color: var(--main-color);
}

